.meter {
    height: 20px;
    position: relative;
    background: rgb(243, 239, 239);
    -moz-border-radius: 25px;
    -webkit-border-radius: 25px;
    border-radius: 25px;
  }
  .meter > span {
    display: block;
    height: 100%;
    border-top-right-radius: 6px;
    border-bottom-right-radius: 6px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    background-color: #aaa5ff;
    position: relative;
    overflow: hidden;
  }
  .skill {
  margin-top: 10px;
    line-height: 3.5vw;
  }
  .skill-bar {
    font-size: 28px;
    width: 40%;
  }
  .skills-container {
    margin: 0 auto;
    display: flex;
    padding: 0 10px;
    margin: 0px auto;
    margin-top: 2rem;
  }
  .skills-container > * {
    width: 40vw;
    flex: 1;
    margin-bottom: 30px;
  }
  .skills-image > img {
    margin-left: 80px;
    max-width: 90%;
    height: auto;
  }
  
  /* Media Query */
  
  @media (max-width: 1456px) {
    .skills-bar {
      line-height: 4rem;
    }
    .skills-image {
      display: none;
      order: 2;
    }
  }
  
  @media (max-width: 768px) {
    .skills-container > * {
        width: 70vw;
    }
    .skills-container {
      flex-direction: column;
    }
    .skills-bar {
      line-height: 3rem;
      margin: 20px;
    }
    .skills-image {
      display: none;
      order: 2;
    }
    .skill > p {
      padding: 10px;
    }
  }